<template>
    <view class="content" style="text-align: center;width: 90%;">
        <uni-segmented-control  style="width: 100%;" :current="current" :values="items" @clickItem="onClickItem" style-type="button" active-color="#007AFF"></uni-segmented-control>
		<view v-if="current === 0">
			<uni-collapse accordion="true">
				<uni-collapse-item title="教师简介" style="text-align: left;">
					
				</uni-collapse-item>
				<uni-collapse-item title="课程简介" style="text-align: left;">
					
				</uni-collapse-item>
				<uni-collapse-item title="教学大纲" style="text-align: left;">
					
				</uni-collapse-item>
				<uni-collapse-item title="学习指南" style="text-align: left;">
					
				</uni-collapse-item>
				<uni-collapse-item title="参考资料" style="text-align: left;">
					
				</uni-collapse-item>
			</uni-collapse>
		</view>
		<view v-if="current === 1">
			
		</view>
		<view v-if="current === 2">
			
		</view>
    </view>
</template>

<script>
	import jyfParser from "@/components/jyf-parser/jyf-parser";
	import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
	
	export default {
		components: {uniSegmentedControl,jyfParser},
		data() {
			return {
				items: ['简介','章节','更多'],
				current: 0,
				courseId:"",
				planCourse:{},
				courseDetail:uni.getStorageSync("courseNotice")
			}
		},
		onPullDownRefresh() {
			this.loadJs.reloadCourseDetail(this.courseId,this.$store.baseInfoId);
			this.courseDetail = uni.getStorageSync("courseDetail");
			uni.stopPullDownRefresh();
		},
		onLoad(option) {
			console.log(option)
			this.planCourse = option;
		},
		async onReady() {
			if(!uni.getStorageSync("courseDetail")){
				await this.loadJs.reloadCourseDetail(this.courseId,this.$store.baseInfoId);
			}
			this.courseDetail = uni.getStorageSync("courseDetail");
		},
		methods: {
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
			}
		}
	}
</script>

